import * as tf from "@tensorflow/tfjs";

import { returnDinoPopulation } from "./Dino";
import { Bush } from "./Bush";
import config from "./configuration.json";

let population = 10;
let dinos = [];

let b;

function sketch(p) {
  p.setup = () => {
    tf.setBackend("cpu");
    p.createCanvas(config.GAME_WIDTH, config.GAME_HEIGHT);
    dinos = returnDinoPopulation(population, p);

    console.log(dinos);
    b = new Bush(config.GAME_WIDTH + 40, 3, 3, p);
  };
  p.draw = () => {
    p.background(255, 204, 0);
    p.line(
      0,
      -(100 - config.GAME_HEIGHT),
      config.GAME_WIDTH,
      -(100 - config.GAME_HEIGHT)
    );
    b.show();
    b.update();
  };
}

export default sketch;
